.hero-container {
    /* height: 786px; */
    background: url("../../images/bg.svg");
    width:100%;
}

.hero-heading {
    margin-top: 15%;
    margin-left:30px;
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 56px !important;
    line-height: 120%;
    /* or 67px */
    
    
    color: #1E266D;
}

.hero-content {
    margin-left: 10%;
font-family: Inter;
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 150%;
/* or 27px */


color: #455880;
}

.hero-btn {
    margin-top:5%;
    margin-left: 10%;
    width: 188.06px;
height: 50px;
font-family: Inter;
font-style: normal;
font-weight: normal;
font-size: 18px;
/* line-height: 100%; */
/* identical to box height, or 18px */

display: flex;
align-items: center;
text-align: center;
border-radius: 100px;
padding-left: 48px;

color: #FFFFFF;
background-color: #1E266D;
}
.hero-img {
    margin-top:20%;
    width:140%;
}
.vision-cont {
width: 100%;
height: 200px;
color: #FFFFFF;
background: #1E266D;

}
.vision-sec {
    height: 100%;
    text-align: center;
}

.vision-title {
    padding-top:60px;
    font-size: 18px !important;
}

.vision-sub-titile {
    font-size: 22px !important;
}

.vision-body {
    margin-top:5%;
    margin-left: 10%;
    width: 80%;
height: 590px;
}

.vision-body h5 {
    font-family: Inter;
font-style: normal;
font-weight: bold;
font-size: 48px !important;
line-height: 60px;
/* identical to box height, or 125% */

text-align: center;

color: #1E266D;
}
.vision-left-cont {
    padding-top:60px;
}

.sub-heading {
    padding-top:30px;
}

.vision-end {
    padding-left:10%;
    width:90%;
}

.location-head-sec {
    width: 100%;
height: 60px;
/* left: 10; */

font-family: Inter;
font-style: normal;
font-weight: bold !important;
font-size: 48px !important;
line-height: 60px;
/* identical to box height, or 125% */

text-align: center;

color: #1E266D;
}

.location-cards {
    margin-left:12%;
    margin-bottom:30px;
}

.end-location-list {
    margin-left:22%;
}

.card {
    /* position: absolute; */
width: 273px !important;
height: 305px;
display:inline-block !important;
margin-right:15px;
/* left: 12%;
top: 2402px; */
}

.card-img-top {
    height:200px;
}


@media screen and (min-width:900px) and (max-width:1350px) {
    .hero-heading{
    margin-top: 34%;
    font-size: 36px !important;
    }
    .hero-container {
        height: 529px !important;
    }
    .vision-cont {
        width: 100%;
        height: 170px;
        color: #FFFFFF;
        background: #1E266D;
        
        }
        .card {
            width: 195px !important;
        }
}

@media screen and (min-width:280px) and (max-width:899px) {

    .hero-heading{
        margin-top: 20%;
        font-size: 30px !important;
        }
        .hero-img {
            width:100%;
        }
        .vision-cont {
            display: hidden;
            visibility: hidden;
        }
        .vision-end {
            margin-top: 40%;
        }
        .end-location-list {
            margin-left:13%;
        }
}

@media screen and (min-width:280px) and (max-width:500px) {
    .vision-end {
        margin-top: 83%;
    }
}