.tech-con{
    margin:30px 10px;
    width:80%;
}
.row-sec {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.tech-cont-dec{
    margin-top:30px;
    color: #1E266D;
}

.tech-card{
    background: #FFFFFF;
    box-shadow: 2px 4px 12px rgba(30, 38, 109, 0.1);
    border-radius: 10px !important;
    padding: 10px !important;
    /* border: 2px solid; */
    border-top: 6px solid #1E266D !important;
    margin-bottom:10px;
    height: 400px;
    overflow-y:auto;
}

.tech-sub-head {
    width:100%;
    text-align: center;
}

.tech-card .card-body{
    font-size: 14px !important;
}