.contact-container{
width: 100%;
min-height: 1000px;
/* left: -13px; */
top: 80px;

/* background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 42.12%, rgba(23, 42, 180, 0.036) 179.69%); */
border-radius: 0px;
}
.hero-sec{
    background: url('../../images/bg.svg');
    width: 100%;
    height: 411px;
    /* left: -13px; */
    
    /* background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 42.12%, rgba(23, 42, 180, 0.036) 179.69%); */
    border-radius: 0px;
}
.contact-header-sec {
height: 243.23px;
text-align: center;
margin-top: 15.77px;
}

.contact-body {
    width: 501px;
    min-height: 800px;
    margin: 10px 30%;
    position: absolute;
    top:320px;
    background: #FFFFFF;
    box-shadow: 2px 4px 12px rgba(30, 38, 109, 0.1);
    border-radius: 10px;
}

.form-field-input{
    width: 301px;
height: 41px;
/* left: 510px;
top: 527px; */

background: #F6F6F6;
box-shadow: inset 0px 2px 5px rgba(30, 38, 109, 0.1);
border-radius: 10px;
border:0px;
}
.form-field-label {
    color: #1E266D;
    margin-top: 42px;
    width: 356px;
    display: block;
}
.popup-form-field{
    background: #F6F6F6;
box-shadow: inset 0px 2px 5px rgba(30, 38, 109, 0.1);
border-radius: 10px;
border:0px;
width:90%;
height: 60px;
}
.form-field-desc {
width: 301px;
height: 151px;
background: #F6F6F6;
box-shadow: inset 0px 2px 5px rgba(30, 38, 109, 0.1);
border-radius: 10px;
border:0px;
}
.form-btn {
width: 185px;
height: 60px;
border:0px;
position: relative;
top:30px;
background: #172AB4;
border-radius: 100px;
color:#fff;
font-family: Inter;
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 125%;
}
form {
    margin-left: 76px;
  
}
.PhoneInputInput{
        border: 0 !important;
background: transparent !important;
}
.phone-input-field {
    width:245px !important;
    margin-left:10px;
}
.form-field-select-input{
    width:50px;
    height: 41px;
/* left: 510px;
top: 527px; */

background: #F6F6F6;
box-shadow: inset 0px 2px 5px rgba(30, 38, 109, 0.1);
border-radius: 10px;
border:0px;
}
@media screen and (min-width:900px) and (max-width:1300px) {
    .contact-body {
        margin-left:17% !important;
    }
    .contact-header-sec {
        margin-left: 17%;
    }
}

.disable-btn{
    cursor: not-allowed;
    pointer-events: none;

    /*Button disabled - CSS color class*/
    color: #c0c0c0;
    opacity: 0.8;
}

@media screen and (min-width:280px) and (max-width:890px) {
    .contact-header-sec{
        width:100%;
        margin-left: 0px;
    }
    .contact-body{
        width:80%;
        margin-left:8%;
    }
    .form-field-input {
        width: 90%;
    }
    .form-field-desc {
        width: 90%;
    }
}